import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';
import './Header.scss';

function Header() {
    return (
        <header className="header" id="Top">
            <a href="" className="tel">
                <FontAwesomeIcon icon={['fab', 'whatsapp']} size="2x" /> 
                <span>+8 765 445 21 32</span>
            </a>
            <nav className="header-menu">
                <a className="header-menu__item" href="#AboutUs">О нас</a>
                <a className="header-menu__item" href="#Services">Наши услуги</a>
                <a className="header-menu__item" href="#Contacts">Где нас найти</a>
            </nav>
            <a href="" className="socials">
                <span>Наш instagram:</span>     
                <FontAwesomeIcon icon={['fab', 'instagram']} size="2x" />
            </a>
            <nav className="mobile-menu">
                <div className="brands">
                    <FontAwesomeIcon icon={['fab', 'whatsapp']} size="2x" /> 
                    <FontAwesomeIcon icon={['fab', 'instagram']} size="2x" />
                </div>                
                <input type="checkbox" id="checkbox" className="mobile-menu__checkbox" />
                <label htmlFor="checkbox" className="mobile-menu__btn">
                    <div className="mobile-menu__icon"></div>
                </label>
                <div className="mobile-menu__container">
                <ul className="mobile-menu__list">
                    <li className="mobile-menu__item">
                        <a href="#AboutUs" className="mobile-menu__link">О нас</a>
                    </li>
                    <li className="mobile-menu__item">
                        <a href="#Services" className="mobile-menu__link">Наши услуги</a>
                    </li>
                    <li className="mobile-menu__item">
                        <a href="#Contacts" className="mobile-menu__link">Где нас найти</a>
                    </li>
                </ul>
                </div>
            </nav>
        </header>
    )
}

export default Header
